import { Playlist, PlayableTrack } from './interfaces';
import { Machine } from 'xstate';

interface PlayerStateSchema {
    states: {
        playlist: {
            states: {
                ready: {};
                addingTrack: {};
            };
        };
        playback: {
            states: {
                idle: {};
                loading: {};
                readyForPlayback: {};
                startPlaying: {};
                startPausing: {};
                playing: {};
                paused: {};
                failedPlaying: {};
                failedPausing: {};
                finished: {};
            };
        };
    };
}

interface PlayerStateContext {
    currentPlaylist: Playlist;
    currentTrackIndex: number;
    currentTimeInTrackMilliseconds: number;
}

// Event types handled by the player state machine
export type AddTrackEvent = { type: 'ADD_TRACK'; track: PlayableTrack };
export type PlayerStateEvent =
    | AddTrackEvent
    | { type: 'LOAD' }
    | { type: 'PLAY' }
    | { type: 'PAUSE' }
    | { type: 'RETRY' };

export const createPlayerStateMachine = (playerId: string) => {
    return Machine<PlayerStateContext, PlayerStateSchema, PlayerStateEvent>({
        id: playerId,
        type: 'parallel',
        context: {
            currentPlaylist: new Playlist(),
            currentTrackIndex: 0,
            currentTimeInTrackMilliseconds: 0.0,
        },
        states: {
            playlist: {
                initial: 'ready',
                states: {
                    ready: {
                        on: {
                            ADD_TRACK: 'addingTrack',
                        },
                    },
                    addingTrack: {
                        invoke: {
                            id: 'addingTrack',
                            src: (context, event: AddTrackEvent) => {
                                const result = context.currentPlaylist.addTrack(event.track);
                                if (result) {
                                    return Promise.resolve(true);
                                } else {
                                    return Promise.reject(false);
                                }
                            },
                            onDone: 'ready',
                            onError: 'ready',
                        },
                    },
                },
            },
            playback: {
                initial: 'idle',
                states: {
                    idle: {
                        on: {
                            LOAD: 'loading',
                        },
                    },
                    loading: {
                        invoke: {
                            id: 'loadPlayer',
                            src: (context, _) => {
                                if (context.currentTrackIndex >= context.currentPlaylist.tracks.length) {
                                    return Promise.reject('empty playlist');
                                }
                                // Playlist has at least one track, so intialize the track at current index.
                                return context.currentPlaylist.tracks[context.currentTrackIndex].mediaProvider.init();
                            },
                            onDone: { target: 'readyForPlayback' },
                            onError: { target: 'idle' },
                        },
                    },
                    readyForPlayback: {
                        on: {
                            PLAY: 'startPlaying',
                            PAUSE: 'startPausing',
                        },
                    },
                    startPlaying: {
                        invoke: {
                            id: 'startPlayback',
                            src: (context, _) => {
                                const currentTrack = context.currentPlaylist.tracks[context.currentTrackIndex!];
                                return currentTrack.mediaProvider.play();
                            },
                            onDone: {
                                target: 'playing',
                            },
                            onError: {
                                target: 'failedPlaying',
                            },
                        },
                    },
                    startPausing: {
                        invoke: {
                            id: 'pausePlayback',
                            src: (context, _) => {
                                const currentTrack = context.currentPlaylist.tracks[context.currentTrackIndex!];
                                return currentTrack.mediaProvider.pause();
                            },
                            onDone: {
                                target: 'paused',
                            },
                            onError: {
                                target: 'failedPausing',
                            },
                        },
                    },
                    playing: {
                        on: {
                            PAUSE: 'startPausing',
                        },
                    },
                    paused: {
                        on: {
                            PLAY: 'startPlaying',
                        },
                    },
                    failedPlaying: {
                        on: {
                            RETRY: 'startPlaying',
                        },
                    },
                    failedPausing: {
                        on: {
                            RETRY: 'startPausing',
                        },
                    },
                    finished: {
                        type: 'final',
                    },
                },
            },
        },
    });
};
